import express from 'express';
import XController from '../controllers/Xsignup';
import registerUser from '../controllers/signup';
import signinUser from '../controllers/signin';
import retrieveUser from '../controllers/allUser';
import verfy_admin from '../middlewares/checkAdmin';

const authapp = express.Router();
authapp.post('/students', XController.insert)
  .get('/students', XController.retrieve)
  .get('/students/:id', XController.retrieveOne)
  .patch('/students/:id', XController.update)
  .delete('/students/:id', XController.delete)

  .post('/auth/signup', registerUser.signupUser)
  .post('/auth/signin', signinUser.loginUser)
  .get('/users', verfy_admin, retrieveUser.retrieveAllUsers);

export default authapp;
